<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="6">
                <h2>Редактирование плана</h2>
                <v-form v-if="plan">
                    <v-text-field
                            label="Название"
                            v-model="plan.name"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-text-field
                            label="Стоимость"
                            type="number"
                            v-model="plan.price"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-text-field
                            label="ID провайдера"
                            v-model="plan.provider_id"
                    ></v-text-field>
                    <v-btn @click="submit" :disabled="!plan.name || !plan.price" class="primary">
                        Обновить план
                    </v-btn>
                    <v-btn @click="dialog = true" class="secondary ml-5">
                        Удалить план
                    </v-btn>
                </v-form>
            </v-col>
        </v-row>

        <v-dialog
                v-if="dialog"
                v-model="dialog"
                width="500"
        >

            <v-card>
                <v-card-title
                        class="headline grey lighten-2"
                        primary-title
                >
                    Удаление плана
                </v-card-title>

                <v-card-text>
                    <p>Вы действительно хотите удалить план?</p>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="primary"
                            text
                            @click="remove"
                    >
                        Да
                    </v-btn>
                    <v-btn
                            color="primary"
                            text
                            @click="dialog = false"
                    >
                        нет
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'

    export default {
        name: "AddPlan",
        data: () => ({
            plan: null,
            dialog: false,
        }),
        mounted() {
            this.getPlan()
        },
        methods: {
            ...mapActions(['setAlert']),
            async getPlan() {
                const res = await this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/${this.$route.params.id}`,
                    method: 'GET'
                })
                this.plan = res.data
            },
            remove() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/${this.$route.params.id}`, method: 'DELETE'}).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'План удален'
                    })
                    this.$router.push('/plans')
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            },
            submit() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/${this.$route.params.id}`, method: 'PUT', data: {
                        name: this.plan.name,
                        price: this.plan.price,
                        provider_id: this.plan.provider_id,
                    }
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'План обновлен'
                    })
                    this.$router.push('/plans')
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            }
        }
    }
</script>

